import React,{useEffect,useState} from 'react';
import borde from '../../medias/images/borde.gif';

const Grid4Avanced =(props)=>{
    const[prop,setProp] = useState({backgrounds : [],images : [],title : [], columnVisible : []});
    const img = "https://bulma.io/images/placeholders/1280x960.png";
    useEffect(()=>{
        try{
            if(props.images!=null)if(props.images.length>5)throw new Error("Array Index of Exception");
            if(props.backgrounds!=null)if(props.backgrounds.length>5)throw new Error("Array Index of Exception");
            if(props.title!=null)if(props.title.length>4)throw new Error("Array Index of Exception");
            if(props.columnVisible!=null)if(props.columnVisible.length>4)throw new Error("Array Index of Exception");
            setProp({
                backgrounds : props.backgrounds||["red","red","red","red","red"],
                images : props.images||["","","","",""],
                title : props.title||["title","title","title","title"],
                columnVisible : props.columnVisible||["visible","visible","visible","visible"]
            })
        }catch(e){
            console.error(e)
        }

    },[])
    return(
        <div className="columns is-multiline"  style={{background:prop.backgrounds[0],height:"auto",marginLeft:"-1.5rem",marginRight:"-1.5rem",position:"relative"}}>
            <img style={{position:"absolute",left:"0",zIndex:"-1",top:"-40px"}} src={borde} alt="borde"/>
            <div className="column">
                <div class="card">
                    <div class="card-content">
                    <p class="title">
                            El mejor
                            {"\u00a0"}
                            <b style={{color:"red",textShadow:"2px 2px 1px #ffffff",padding:"2px"}}>
                               Contenido 
                            </b>
                            {"\u00a0"}
                            para que aprendas 
                            <span style={{color:"red",textShadow:"2px 2px 1px #ffffff",padding:"2px"}}>
                                sin contratiempos
                            </span>
                        </p>
                        <p class="subtitle">
                            visita cursos
                        </p>
                    </div>
                    <footer class="card-footer">
                        <p class="card-footer-item">
                        <span>
                            View on <a href="https://twitter.com/codinghorror/status/506010907021828096">Twitter</a>
                        </span>
                        </p>
                        <p class="card-footer-item">
                        <span>
                            Share on <a href="#">Facebook</a>
                        </span>
                        </p>
                    </footer>
                </div>
            </div>
            <div className="column">
            <div class="card">
                    <div class="card-content">
                    <p class="title">
                            <b style={{color:"#8b72ee",textShadow:"2px 2px 1px #ffffff",padding:"2px"}}>
                                Disfruta
                            </b>
                            {"\u00a0"}
                            de acceso de por 
                            <span style={{color:"#8b72ee",textShadow:"2px 2px 1px #ffffff",padding:"2px"}}>
                            vida 
                            </span>
                        </p>
                        <p class="subtitle">
                            visita cursos
                        </p>
                    </div>
                    <footer class="card-footer">
                        <p class="card-footer-item">
                        <span>
                            View on <a href="https://twitter.com/codinghorror/status/506010907021828096">Twitter</a>
                        </span>
                        </p>
                        <p class="card-footer-item">
                        <span>
                            Share on <a href="#">Facebook</a>
                        </span>
                        </p>
                    </footer>
                </div>
            </div>
            <div className="column">
            <div class="card">
                    <div class="card-content">
                        <p class="title">
                            <b style={{color:"#00d1b2",textShadow:"2px 2px 1px #ffffff",padding:"2px"}}>
                                Cursos
                            </b>
                            {"\u00a0"}
                            desde fundamentos de 
                            <span style={{color:"#00d1b2",textShadow:"2px 2px 1px #ffffff",padding:"2px"}}>
                                Programación
                            </span>
                            hasta experto
                        </p>
                        <p class="subtitle">
                            visita cursos
                        </p>
                    </div>
                    <footer class="card-footer">
                        <p class="card-footer-item">
                        <span>
                            View on <a href="https://twitter.com/codinghorror/status/506010907021828096">Twitter</a>
                        </span>
                        </p>
                        <p class="card-footer-item">
                        <span>
                            Share on <a href="#">Facebook</a>
                        </span>
                        </p>
                    </footer>
                </div>
            </div>
        </div>
    );
}
export default Grid4Avanced;